@font-face {
  font-family: AlterwaveExtraBold;
  src: url(/pitchblack-master/static/media/Alterwave-ExtraBold.8deeecb1.otf);
}
@font-face {
  font-family: AlterwaveBold;
  src: url(/pitchblack-master/static/media/Alterwave-Bold.fa07b4c5.otf);
}
@font-face {
  font-family: AlterwaveRegular;
  src: url(/pitchblack-master/static/media/Alterwave-Regular.630c2bc2.otf);
}
@font-face {
  font-family: AlterwaveLight;
  src: url(/pitchblack-master/static/media/Alterwave-Light.f115dcf2.otf);
}
.burger_icon {
  display: none;
}
a {
  text-decoration: none;
}
* {
  margin: 0px;
  padding: 0px;
  box-sizing: border-box !important;
}
ul,
nav {
  list-style: none;
}
#root {
  height: 100% !important;
  width: 100% !important;
}
#root > div {
  width: 100% !important;
  height: 100% !important;
}
.common_width {
  width: 1300px;
  margin: 0rem auto !important;
}
.home_banner {
  height: 800px;
  background-size: contain;
  background: url("/pitchblack-master/static/media/Mask group.39e499fd.png");
  position: relative;
  z-index: 9000;
  display: flex;
  flex-direction: column;
}

.Header .content,
.Header .content nav {
  display: flex;
  align-items: center;
}
.Header .content {
  justify-content: space-between;
  padding: 2rem 0rem !important;
  /* background-color: red; */
}
.Header .content nav > * + * {
  margin-left: 3rem;
}
.Header .content nav a {
  color: #72554a;
  font-size: 16px;
  font-weight: 300;
  line-height: 15px;
  letter-spacing: 0em;
  display: flex;
  cursor: pointer;
  align-items: center;
  font-family: AlterwaveRegular;
  padding: 0.8rem !important;
  transition: 0.3s ease all;
}

.Header .content nav .pitch_list a:hover {
  border-radius: 5px;
  background-color: #f0efef;
}
.Header .content nav a img {
  margin-left: 5px;
}

.Header .content nav a.button {
  height: 51px;
  width: 195px;
  justify-content: center;
  border-radius: 6.419753074645996px;
  border: 1px solid #72554a;
  font-family: AlterwaveRegular;
  font-size: 13px;
  font-weight: 400;
  line-height: 12px;
  letter-spacing: -0.03em;
  text-transform: uppercase;
  transition: 0.3s ease all;
}
.Header .content nav a.button:hover {
  background-color: #72554a;
  color: #f8eadd;
}
.Header .content nav a.button svg {
  margin-left: 6px;
}
.Header .content nav a.button:hover svg path {
  stroke: #f8eadd;
}
.home_banner .body_area {
  flex: 1 1;
}
.home_banner .body_area .content {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  height: 100%;
  width: 50%;
  text-align: center;
}
.home_banner .body_area .content h1 {
  font-size: 54px;
  font-weight: 800;
  line-height: 62px;
  letter-spacing: -0.02em;
  text-align: center;
  font-family: AlterwaveExtraBold;
  color: #333333;
}
.home_banner .body_area .content p {
  font-family: "Poppins", sans-serif;
  font-size: 18px;
  font-weight: 400;
  line-height: 27px;
  letter-spacing: 0em;
  text-align: center;
  color: #4d4d4d;
  margin-top: 1.5rem;
  margin-bottom: 4rem;
}
.home_banner .body_area .content a {
  height: 64px;
  width: 243px;

  display: flex;
  justify-content: center;
  align-items: center;
  border-radius: 8px;
  background: #795d53;
  border: 1px solid #795d53;
  font-family: AlterwaveRegular;
  font-size: 16px;
  font-weight: 400;
  line-height: 24px;
  letter-spacing: -0.03em;
  text-align: center;
  color: #f8eadd;
  text-transform: uppercase;
  transition: 0.3s ease all;
}
.home_banner .body_area .content a img {
  margin-left: 5px;
}
.home_banner .body_area .button svg {
  margin-left: 5px;
}
.home_banner .body_area .button:hover {
  background: transparent;
  color: #795d53;
}
.home_banner .body_area .button:hover svg path {
  stroke: #795d53;
}
/* BlackFounderConnection */
.BlackFounderConnection {
  background: #362c28 !important;
  padding: 8rem 0rem;
}
.BlackFounderConnection .content > h1 {
  font-size: 32px;
  width: 40%;
  font-weight: 700;
  line-height: 48px;
  letter-spacing: -0.02em;
  font-family: AlterwaveBold;
  text-align: left;
  color: #fff;
}

.BlackFounderConnection .cards_wrapper {
  margin-top: 4rem;
  display: grid;
  grid-template-columns: 1fr 1fr;
}
.BlackFounderConnection .cards_wrapper > * {
  background: #58443e66;
  padding: 5rem 3rem;
  height: 430px;
  border-top: 4px solid #bc8d7e;
  border-radius: 0px;
  cursor: pointer;
}
.BlackFounderConnection .cards_wrapper h1 {
  font-size: 24px;
  font-weight: 300;
  line-height: 36px;
  letter-spacing: 0em;
  text-align: left;
  color: #fff;
  margin-bottom: 2rem;
}
.BlackFounderConnection .cards_wrapper p {
  font-family: Poppins;
  font-size: 16px;
  font-weight: 400;
  line-height: 27px;
  letter-spacing: 0em;
  text-align: left;
  margin-bottom: 1rem;
  color: #fff;
}

/* active state */
.BlackFounderConnection .cards_wrapper > *.notactive {
  background-color: transparent;
  border-top: 2px solid #fff;
}
.BlackFounderConnection .cards_wrapper > *.notactive h1 {
  color: #9b9694;
}
.BlackFounderConnection .cards_wrapper > *.notactive p {
  color: #9b9694;
}

/* become founder */
.BannerSmall {
  padding: 7rem 0rem;
}
.BannerSmall .content {
  display: grid;
  grid-template-columns: 1fr 1fr;
  grid-gap: 1rem;
  width: 80%;
}
.BannerSmall .presentation h1 {
  font-family: AlterwaveBold;
  font-size: 32px;
  font-weight: 700;
  line-height: 48px;
  letter-spacing: -0.02em;
  text-align: left;
  color: #333333;
}
.BannerSmall .presentation p {
  font-family: Poppins;
  font-size: 16px;
  font-weight: 400;
  line-height: 27px;
  letter-spacing: 0em;
  text-align: left;
  color: #333333;
  margin-top: 1.5rem;
  width: 80%;
}
.BannerSmall a {
  align-self: flex-end;
  justify-self: flex-end;
  height: 64px;
  width: 243px;
  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: 6.419753074645996px;
  color: #fff;
  background: #795d53;
  font-family: AlterwaveRegular;
  font-size: 16px;
  font-weight: 400;
  line-height: 12px;
  letter-spacing: -0.03em;
  text-transform: uppercase;
}
.BannerSmall a img {
  margin-left: 5px;
}

/* footer */
.footer {
  background: #362c28;
}

.footer .content {
  display: flex;
  justify-content: space-between;
  height: 140px;
  align-items: center;
}
.footer .content nav {
  display: flex;
  align-items: center;
}
.footer .content nav > * + * {
  margin-left: 3rem;
}

@media screen and (max-width: 1350px) {
  .common_width {
    width: 95%;
  }
}
@media screen and (max-width: 1200px) {
  .home_banner .body_area .content {
    width: 80%;
  }
  .BlackFounderConnection .content > h1 {
    width: 70%;
  }
}

@media screen and (max-width: 1050px) {
  .BlackFounderConnection .cards_wrapper {
    grid-template-columns: 1fr;
    grid-row-gap: 2rem;
  }
  .BannerSmall .content {
    width: 95%;
    grid-template-columns: 1fr;
  }
}
@media screen and (max-width: 600px) {
  .Header nav > * + * {
    display: none;
  }
  .home_banner .body_area .content h1 {
    font-size: 42px;
  }
  .home_banner .body_area .content p {
    font-size: 16px;
  }
  .home_banner {
    height: 100vh;
  }
  .home_banner {
    background-position: 0px 70vh;
    background-size: 100%;
    background-repeat: no-repeat;
  }
  .burger_icon {
    display: block;
  }
  .BlackFounderConnection .content > h1 {
    width: 100%;
    text-align: center;
  }
  .BlackFounderConnection .cards_wrapper > * {
    height: 100%;
  }
  .BannerSmall .presentation p,
  .BannerSmall .presentation h1 {
    width: 100%;
    text-align: center;
  }
  .BannerSmall a {
    align-self: center;
    justify-self: center;
  }
  .footer .content {
    height: 200px;
  }
  .footer .content {
    flex-direction: column;
    align-items: center;
    justify-content: center;
  }
  .footer .content > * + * {
    margin-top: 40px;
  }
}

.waitlist_wrapper {
  background-size: contain;
  background: url("/pitchblack-master/static/media/Mask group.39e499fd.png");
  position: relative;
  display: flex;
  min-height: 100vh;
  flex-direction: column;
}
.waitlist_wrapper .MuiOutlinedInput-notchedOutline {
  border: none !important;
}
.waitlist_wrapper .content {
  flex: 1 1;
  margin-bottom: 3rem !important;
}
.waitlist_wrapper .form_wrapper {
  height: 100%;
}
.waitlist_wrapper .form_wrapper > h1,
.WaitlistFormSelection > h1 {
  font-family: AlterwaveBold;
  font-size: 32px;
  font-weight: 700;
  line-height: 48px;
  letter-spacing: -0.02em;
  text-align: center;
  color: #000;
}

.waitlist_wrapper .form_wrapper > p,
.WaitlistFormSelection > p {
  font-family: "Poppins", sans-serif;
  font-size: 18px;
  font-weight: 400;
  line-height: 27px;
  letter-spacing: 0em;
  text-align: center;
  color: #4d4d4d;
  margin-top: 1.5rem;
  margin-bottom: 4rem;
  width: 35%;
  margin: 1rem auto;
}
.waitlist_wrapper .form_wrapper > .input_container {
  display: grid;

  grid-template-columns: 1fr 1fr 1fr 1fr;
  align-items: center;
  grid-gap: 1.5rem;
  width: 45%;
  margin: 0rem auto;
  margin-top: 5rem;
}
.waitlist_wrapper .form_wrapper > .input_container > * {
  width: 100%;
}
.waitlist_wrapper .form_wrapper > .input_container > *:nth-child(6) {
  justify-self: center;
}
.waitlist_wrapper .form_wrapper .grid_2 {
  grid-column: span 2;
}
.waitlist_wrapper .form_wrapper .grid_4 {
  grid-column: span 4;
}
.waitlist_wrapper .form_wrapper .grid_3 {
  grid-column: span 3;
}
.waitlist_wrapper .form_wrapper > .input_container label {
  font-family: Poppins;
  font-size: 14px;
  font-weight: 400;
  line-height: 21px;
  letter-spacing: 0em;
  text-align: left;
  color: #333;
}
.waitlist_wrapper .form_wrapper > .input_container input {
  display: block;
  width: 100%;
  font-family: Poppins;
  height: 55px;
  outline: none;
  border: 1px solid #dabcae;
  background-color: transparent;
  border-radius: 10px;
  padding: 0rem 1rem;
  font-size: 16px;
  margin-top: 20px;
  color: #000 !important;
}
.waitlist_wrapper .form_wrapper button {
  background-color: transparent;
  border: none;
  cursor: pointer;
}
.waitlist_wrapper .form_wrapper button {
  height: 64px;

  border-radius: 8px;
  background: #795d53;
  color: #fff;
  font-family: AlterwaveRegular;
  font-size: 16px;
  text-transform: uppercase;
  font-weight: 400;
  line-height: 24px;
  letter-spacing: -0.03em;
  text-align: center;
}

@media screen and (max-width: 1000px) {
  .waitlist_wrapper .form_wrapper {
    width: 95%;
    margin-left: auto;
    margin-right: auto;
  }
  .waitlist_wrapper .form_wrapper > .input_container,
  .waitlist_wrapper .form_wrapper > p {
    width: 100%;
  }
  .waitlist_wrapper .form_wrapper .grid_2 {
    grid-column: span 4;
  }
  .waitlist_wrapper .form_wrapper .grid_4 {
    grid-column: span 4;
  }
  /* .waitlist_wrapper .form_wrapper .grid_3 {
    grid-column: span 3;
  } */
}

@media screen and (max-width: 600px) {
  .waitlist_wrapper .form_wrapper > h1 {
    font-size: 28px;
  }
  .waitlist_wrapper .form_wrapper > p {
    font-size: 16px;
  }
  .waitlist_wrapper .form_wrapper > .input_container label {
    font-size: 12px;
  }
  .waitlist_wrapper .form_wrapper button {
    font-size: 13px;
  }
  .waitlist_wrapper .form_wrapper button {
    height: 55px;
  }
  .waitlist_wrapper .form_wrapper > .input_container {
    margin-top: 3rem;
  }
  .waitlist_wrapper .form_wrapper {
    width: 95%;
    margin-left: auto;
    margin-right: auto;
  }
  .waitlist_wrapper {
    background-position: 0px 80vh;
    background-size: 100%;
    background-repeat: no-repeat;
  }
}
.WaitlistFormSelection {
  width: 55%;
  margin: 0rem auto;
}
.WaitlistFormSelection .card {
  height: 227px;
  width: 100%;
  margin-top: 3rem !important;
  border-radius: 20px;
  border: 1px solid #795d53;
  display: flex;
  flex-direction: column;
  padding: 1rem;
  align-items: center;
  cursor: pointer;
  justify-content: center;
}
.WaitlistFormSelection .card:hover {
  background-color: #795d53;
}
.WaitlistFormSelection .cards_wrapper {
  display: grid;
  grid-template-columns: 1fr 1fr;
  justify-content: space-between;
  grid-gap: 2rem;
}
.WaitlistFormSelection .card h1 {
  font-family: AlterwaveLight;
  font-size: 24px;
  font-weight: 300;
  line-height: 36px;
  letter-spacing: 0em;
  text-align: center;
  color: #333333;
  margin-bottom: 10px;
}
.WaitlistFormSelection .card:hover h1 {
  color: #fff;
}
.WaitlistFormSelection .card p {
  font-family: Poppins;
  font-size: 14px;
  font-weight: 400;
  line-height: 21px;
  letter-spacing: 0em;
  text-align: center;
  color: #808080;
}
.WaitlistFormSelection .card:hover p {
  color: #fff;
}
@media screen and (max-width: 1050px) {
  .WaitlistFormSelection {
    width: 80%;
  }
}
@media screen and (max-width: 700px) {
  .WaitlistFormSelection .cards_wrapper {
    grid-template-columns: 1fr;
  }
  .waitlist_wrapper .form_wrapper > p,
  .WaitlistFormSelection > p {
    width: 100%;
  }
  .WaitlistFormSelection .card + .card {
    margin-top: 10px !important;
  }
}
.waitlist_wrapper .flex_wrapper {
  display: flex;
  transition: 1s ease all;
}
.flex_wrapper .first_class,
.flex_wrapper .second_class {
  min-width: 100%;
}
.waitlist_wrapper.active .flex_wrapper {
  -webkit-transform: translateX(-100%);
          transform: translateX(-100%);
}

