/* checkmark start */
/* ------------------------- */
.Form-container {
    display: block;
    position: relative;
    padding-left: 35px;
    margin-bottom: 20px;
    cursor: pointer;
    font-size: 22px;
    -webkit-user-select: none;
    -ms-user-select: none;
    user-select: none;
}

.Form-container input {
    position: absolute;
    opacity: 0;
    cursor: pointer;
    height: 0;
    width: 0;
}

.Form-container .checkmark {
    position: absolute;
    top: 0;
    left: 0;
    height: 25px;
    width: 25px;
    background-color: #FBEDE0;
}

.Form-container:hover input~.checkmark {
    background-color: #ccc;
}

.Form-container input:checked~.checkmark {
    background-color: #BF907E;
    border: 1px solid #FBEDE0;
}

.Form-container .checkmark:after {
    content: "";
    position: absolute;
    display: none;
}

.Form-container input:checked~.checkmark:after {
    display: block;
}

.Form-container .checkmark:after {
    content: counter(my-counter);
    counter-increment: my-counter 1;
    left: 50%;
    -webkit-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);
    top: 50%;
    font-size: 15px;
    color: #fff;
}

/* ---------------------*/
/* checkmark end */

