input[type="radio"]:after {
  width: 30px;
  height: 30px;
  border-radius: 25px;
  top: -11px;
  left: -11px;
  position: relative;
  background-color: #c4c4c4;
  content: "";
  display: inline-block;
  visibility: visible;
  border: 2px solid white;
  cursor: pointer;
}

input[type="radio"]:checked:after {
  width: 30px;
  height: 30px;
  border-radius: 25px;
  top: -11px;
  left: -11px;
  position: relative;
  background-color: #bf907e;
  content: "";
  display: inline-block;
  visibility: visible;
  border: 2px solid white;
}
